
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 400px;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
}

.errorMessage {
  padding: 10px;
  color: #961d1d;
}
